.Uploader {
    width: 100%;
    height: 100%;
    &--300px {
        height: em(300);
    }
    font-size: em(18,14);
    line-height: em(30,18);
    border-radius: em(3);
    color: $grey;
    cursor: pointer;
    background-image: linear-gradient(-45deg, 
                    hsla(168, 5%, 75%, 0.1) 25%, 
                    transparent 25%, 
                    transparent 50%, 
                    hsla(168, 5%, 75%, 0.1) 50%, 
                    hsla(168, 5%, 75%, 0.1) 75%, 
                    transparent 75%, 
                    transparent);
    background-size: em(30,18) em(30,18);
    
    &-icon {
        font-size: 2em;
    }
}
// Temporary solution for drag and drop
.GridSquare:hover > .Uploader,
.Uploader:hover {
    border-color: $primary-color;
    color: $primary-color;
    background-image: linear-gradient(-45deg, 
                    hsla(206, 100%, 52%, 0.1) 25%, 
                    transparent 25%, 
                    transparent 50%, 
                    hsla(206, 100%, 52%, 0.1) 50%, 
                    hsla(206, 100%, 52%, 0.1) 75%, 
                    transparent 75%, 
                    transparent);
    animation: stripes 5s linear infinite;
}
