.text--uppercase {
  text-transform: uppercase;
}

.text--italic {
  font-style: italic;
}

.text--bold {
  font-weight: 700;
}
.text--blue {
  color: $blue;
}

.text--ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@include utilsGenerator((
  classes: (
    text: (text-align),
  ),
  values: (
    center: center,
    left: left,
    right: right,
    justify: justify
  ),
  responsive: true,
  prefix: ".",
  seperator: "--",
));

@include utilsGenerator((
  classes: (
    text: (font-size),
  ),
  values: (
    '12px': em(12),
    '14px': em(14),
  ),
  prefix: ".",
  seperator: '--',
));

@font-face {
  font-family: 'Icomoon';
  src:  url('../fonts/Icomoon/icomoon.eot?47u4kn');
  src:  url('../fonts/Icomoon/icomoon.eot?47u4kn#iefix') format('embedded-opentype'),
    url('../fonts/Icomoon/icomoon.ttf?47u4kn') format('truetype'),
    url('../fonts/Icomoon/icomoon.woff?47u4kn') format('woff'),
    url('../fonts/Icomoon/iicomoon.svg?47u4kn#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

