.Nav {
  background-color: $white;

  &-logo {
  	height: em(20);
  }

  &-list {
    display: none;
    background-color: $white;

    @include respond-to('md') {
      display: flex;
    }

    &--column {
      display: block;
      
      @include respond-to('md') {
        display: none;
      }

      & > .Nav-item {
        display: block;
      }
    }
  }

  &-anchor {
    color: $grey-dark;
    display: inline-block;
    
    &:hover,
    &:focus {
      color: $primary-color;
    }

    &.isActive {
      color: $primary-color;
      position: relative;
      @include respond-to('md') {
        &:after {
          content: "";
          position: absolute;
          width: 0;
          height: 0;
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
          border-left: 0.5em solid transparent;
          border-right: 0.5em solid transparent;
          border-bottom: 0.5em solid $primary-color;
        }
      }
    }
  }
}
