.Input {
    position: relative;
    // Reserve space for hidden elements
    margin: em(18, 14) 0;
    // Fix floats
    &:after {
        content: "";
        display: table;
        clear: both;
    }
    &-field {
        display: block;
        resize: none;
        border-top: none;
        border-left: none;
        border-right: none;
        border-bottom: em(1) solid $grey;
        outline: none;
        background: none;
        color: $black;
        // Remove appearance for dropdowns
        border-radius: 0;
        appearance: none;
        -moz-appearance: none; /** Firefox */
        -webkit-appearance: none; /** Safari and Chrome */
        &::-ms-expand {
            display: none; /** IE 10+ and FF */
        }
        &--select {
            background-image: url("data:image/svg+xml;base64,");
            background-repeat: no-repeat;
            background-position: 98% center;
            background-size: 0.75em;
            &:focus {
                background-image: url("data:image/svg+xml;base64,");
            }
        }
        &:focus {
            border-width: em(1);
            border-color: $primary-color;
            & + .Input-label .Input-required {
                color: $red;
            }
        }
        /** If input value is invalid and dirty
          * 1. Change border color to red;
          * 2. Change label color to red;
          */
        &.ng-invalid.ng-dirty {
            border-color: $red; /** 1 */
            & + .Input-label {
                color: $red; /** 2 */
            }
        }
        /**
         * Special tempopary fix for datepickers
         * TODO: Find the solution how to avoid this angularjs-datepicker issue.
         * Label element is located after the calendar
         */
        &.ng-invalid.ng-dirty + ._720kb-datepicker-calendar {
            & + .Input-label {
                color: $red;
            }
        }
        &:focus + ._720kb-datepicker-calendar + .Input-label,
        &.ng-not-empty +  ._720kb-datepicker-calendar + .Input-label,
        &.ng-invalid.ng-dirty + ._720kb-datepicker-calendar + .Input-label {
            top: auto;
            bottom: 100%;
            font-size: em(12, 14);
            line-height: em(18, 14);
        }
        &:focus + ._720kb-datepicker-calendar + .Input-label {
            color: $primary-color;
        }
        // If input is neither focused nor dirty show text input cursor
        // ng-dirty is an Angular dynamic class
        // &:not(:focus) + .Input-label,
        // &:not(.ng-not-empty) + .Input-label {
        //     cursor: text;
        // }
        // &:not(:focus):not(.ng-not-empty):not(.ng-invalid.ng-dirty) + .Input-label {
        //     top: 0;
        // }
        // If input is either focused or dirty move the label
        &:focus + .Input-label,
        &.ng-not-empty + .Input-label,
        &.ng-invalid.ng-dirty + .Input-label {
            top: auto;
            bottom: 100%;
            font-size: em(12, 14);
            line-height: em(18, 14);
        }
        &:focus + .Input-label {
            color: $primary-color;
        }
    }
    &-label {
        position: absolute;
        top: 0;
        left: 0;
        color: $grey;
    }
    &-field,
    &-label {
        width: 100%;
        font-size: em(16, 14);
        padding: em(2) em(2) em(3);
        // Small letters are too close to each other
        letter-spacing: 0.02em;
    }
    &-errorSpacer {
        float: right;
        min-height: em(24, 14);
        min-width: em(1, 14);
    }
    &-error,
    &-desc {
        padding: em(6) em(2) em(2);
        font-size: em(12, 14);
        line-height: em(18, 14);
    }
    &-error {
        color: $red;
    }
}
