.Notification {
  padding: em(18) em(16);
  border: 1px solid rgba(50, 65, 95, 0.12);
  box-shadow: 0px 2px 4px 0px rgba(51, 60, 78, 0.20),
              0px 0px 8px 0px rgba(50, 65, 95, 0.12);
  &-icon {
    width: em(38);
    height: em(38);
    border-radius: 100%;
    &.Approved {
      background-color: $green;
    }
    &.Rejected {
      background-color: $red;
    }
  }
  &-text {
    margin-left: em(24);
  }
  &-title {
    margin-bottom: em(8);
  }
}