/// Font family
/// @access private
$font: 'Roboto', sans-serif;

html,
body {
  width: 100%;
  height: 100%;
}
/**
 * 1. Responsive font size for extra small screens
 * 2. Beautify font
 */
body {
  font-family: $font;
  font-size: 0.75rem; /* 1 */
  -webkit-font-smoothing: antialiased; /* 2 */

  @include respond-to('sm') {
    font-size: 0.875rem;
  }
}

p {
  line-height: 1.9;
}

a {
  color: $primary-color;
}

input {
  outline: none;
}
/**
 * Helper classes
 */
.hidden {
  display: none!important;
}
