.Table {
    &-label {
        color: $grey;
        &:hover,
        &.isActive {
            color: $primary-color;
        }
        &--90px {
            width: em(90);
        }
    }
    &-row {
        color: $black;
        &:hover {
            background-color: tint($grey, 90%);
        }
    }
}
