.CommentMeta {
    margin: em(18,14) 0;
    &-name {
        font-size: em(16,14);
    }

    &-task {
    width: em(40);
    height: em(40);
    margin: em(18,14) 0;    
  }

    &-task {
    color: $white;
    border-radius: 100%;
    transform: translateX(10px);
    

    &.isResolve {
      background: $green;
    }

    &.isOpen {
      background: $primary-color;
    }
  }
}
