.Progress {
    position: relative;
    &-container {
        width: em(50);
        height: em(50);
        position: relative;
        overflow: hidden;
        border-radius: 100%;
        color: $grey;
        &.isCurrent {
            color: $orange;
        }
    }
    &-stage {
        margin: 0 auto;
        cursor: pointer;
        transform: scale(0.9);
        &:hover,
        &.isCurrent {
            transform: scale(1.2);
        }
        &.isCurrent + .Progress-meta {
            color: $orange;
        }
        &.isLocked {
            border: em(4) solid tint($grey, 25%);
            background-color: $white;
            color: tint($grey, 25%);
            width: em(50);
            height: em(50);
            border-radius: 100%;
            &:after {
                content: "";
                width: 2em;
                height: 2em;
                background-image: url("data:image/svg+xml;base64,");
                background-color: $white;
                background-position: center center;
                background-repeat:  no-repeat;
                background-size: 60%;
                position: absolute;
                top: -1em;
                right: -1em;
            }
        }
    }
    &-bar {
        position: absolute;
        height: em(4);
        top: em(25);
        background-color: tint($grey, 25%);
        &--done {
            background-color: $green;
        }
    }
    &-circle {
        width: em(25);
        height: em(25);
        background: tint($grey, 25%);
        position: absolute;
        overflow: hidden;
        &--25 {
            top: 0;
            right: 0;
        }
        &--50 {
            right: 0;
            bottom: 0;
        }
        &--75 {
            left: 0;
            bottom: 0;
        }
        &--100 {
            left: 0;
            top: 0;
        }
    }
    &-inner {
        height: 100%;
        background-color: $orange;
        &--25 {
            transform-origin: bottom right;
            transform: translateX(-100%);
        }
        &--50 {
            transform-origin: bottom left;
            transform: translateY(-100%);
        }
        &--75 {
            transform-origin: top left;
            transform: translateX(100%);
        }
        &--100 {
            transform-origin: top right;
            transform: translateY(100%);
        }
    }
    &-number {
        width: 2.5em;
        height: 2.5em;
        background-color: $white;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 100%;
    }
    &-meta {
        color: $grey;
    }
}
