.SearchBox {
    width: 100%;
    // Search icon in dataURI format
    // TODO: Find a better way to place icon inside input field
    background-image: url("data:image/svg+xml;base64,");
    background-repeat: no-repeat;
    background-position: 1em center;
    background-size: 1em;
    font-size: em(18, 14);
    line-height: em(26, 14);
    height: em(55, 18);
    border-bottom: em(1) solid $grey;
    &:focus {
        border-color: $primary-color;
        background-image: url("data:image/svg+xml;base64,");
    }
}
