.Btn {
    border-radius: em(4);
    padding: 0.5em 1em;
    white-space: nowrap;
    cursor: pointer;
    display: inline-block;
    border: 1px solid;
    letter-spacing: 0.01em;
    font-size: 0.875em;
    &:disabled {
        opacity: 0.65;
        cursor: not-allowed;
    }
    &--filter {
        border-color: $grey;
        &.isActive,
        &:hover,
        &:focus {
            color: $primary-color;
            border-color: $primary-color;
        }
    }
    &--green {
        background-color: $green;
        border-color: $green;
        color: $white;
        &:hover,
        &:focus {
            background-color: tint($green, 10%);
            border-color: tint($green, 10%);
        }
    }
    &--red {
        background-color: $red;
        border-color: $red;
        color: $white;
        &:hover,
        &:focus {
            background-color: tint($red, 10%);
            border-color: tint($red, 10%);
        }
    }
    &--blue {
        background-color: $primary-color;
        border-color: $primary-color;
        color: $white;
        &:hover,
        &:focus {
            background-color: tint($primary-color, 10%);
            border-color: tint($primary-color, 10%);
        }
    }
    &--pointer {
    cursor: pointer;
  }   
}
