.Tool {
  position: relative;
  display: inline-block;

  &:hover .Tool-tooltip {
    display: block;
  }

  &-image {
    cursor: crosshair;
  }

  &-tag {
    position: absolute;
    background: $primary-color;
    color: $white;
    // padding: 4px 12px;
    width: em(39);
    height: em(39);
    border: 2px solid $white;
    border-radius: 100px;
    transform: translate(-50%, -50%);
    box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.75);

    &:hover {
      background: hsla(359, 86%, 54%, 0.2);
      color: hsla(0, 100%, 100%, 0);
      cursor: move;
    }
  }

  &-tooltip {
    padding: em(8);
    margin: em(8);
    background: rgba(0,0,0,0.6);
    color: $white;
    font-size: 0.75em;
    border-radius: em(4);
    position: absolute;
    display: none;
  }

  // &.isApproved:after {
