.Modal {
  position: absolute;
  width: em(420);
  box-shadow: 0 0 0 1px rgba(50, 65, 95, 0.12),
    0 0 8px rgba(51, 60, 78, 0.07);
  background: $white;
  border-radius: em(4);
  transform: translate(-50%, em(40));

  &-form {
    padding: 1.25em 2em;
  }

  &-title {
    // font-size: em(30);
    // font-weight: 300;
    font-weight: 600;
    color: shade($grey, 30%);
  }
  &-close {
    color: $grey;
    font-size: 1.4em;
    position:absolute;
    top:em(20);
    right:em(20);
  }
  &-textarea {
    border: 1px solid tint($grey, 10%);

    &:focus {
      border: 1px solid $black;
    }
  }

  // &-close {
  //   width: em(45);
  //   height: em(45);
  //   position: absolute;
  //   top: em(-10);
  //   right: em(-10);
  //   background: $primary-color;
  //   border-radius: em(4);
  // }

  // &-closeSymbol {
  //   color: $white;
  //   transform: rotate(45deg);
  // }
}