.Tabs {
  &-tab {
    font-weight: 700;
    border-top: em(2) solid;
    border-color: $transparent;
    cursor: pointer;
    display: inline-block;
    color: $grey;
    &:hover {
      border-color: $grey;
      color: $black;
    }
    &.isActive {
      // color: $primary-color;
      color: $black;
      border-color: $primary-color;
    }
  }
}
