.Menu {
  position: relative;
  width: em(35);
  height: em(40);
  border: none;
  cursor: pointer;
  margin-left: auto;

  @include respond-to('md') {
    display: none;
  }
}

.Menu span {
  display: block;
  position: absolute;
  top: em(18);
  margin: auto 0;
  height: em(4);
  width: 100%;
  background: $black;
  border-radius: em(2);
}
.Menu span:before,
.Menu span:after {
  display: block;
  position: absolute;
  content: "";
  height: em(4);
  width: 100%;
  background: $black;
  transform-origin: 50% 50%;
  border-radius: em(2);
}

.Menu span:before {
  top: em(-10);
  transition: top .2s ease-in-out;
}

.Menu span:after {
  bottom: em(-10);
  transition: bottom .2s ease-in-out;
}

.Menu.isActive span {
  background: none;
}

.Menu.isActive span:before {
  top: 0;
  transform: rotate(45deg);
  transition: transform .3s .2s ease-in-out;
}

.Menu.isActive span:after {
  bottom: 0;
  transform: rotate(-45deg);
  transition: transform .3s .2s ease-in-out;
}
