/// Font maps, gathered all the fonts of the application
/// @access private
/// @type Map
$font-regular: (
  'family': 'Roboto',
  'file': 'Roboto-Regular',
);
$font-bold: (
  'family': 'Roboto',
  'file': 'Roboto-Bold',
  'weight': 700,
);
$font-thin: (
  'family': 'Roboto',
  'file': 'Roboto-Light',
  'weight': 300,
);

/// Generate font map with the attributes
/// @access private
/// @param {String} $family - Font's name
/// @param {String} $file - Font's file
/// @param {Number} $weight - Font's weight
/// @param {String} $style - Font's style
/// @return {Map}
@function font-face($values...) {
  @return map-zip((family, file, weight, style), $values);
}

/// Generate font-face from the font's attributes
/// @access public
/// @param {String} $face - Font's name
/// @param {String} $path - Path to the font's directory
/// @require @font-face
/// @usage @include font-face('Open Sans Regular')
@mixin font-face($face, $path: '../fonts/') {
  $face: map-merge(( weight: normal, style: normal), $face);
  $path: "#{$path}#{map-get($face,family)}/";
  @font-face {
    font-family: map-get($face,family);
    src: url('#{$path}#{map-get($face, file)}.eot');
    src: url('#{$path}#{map-get($face, file)}.eot?#iefix') format('embedded-opentype'),
         url('#{$path}#{map-get($face, file)}.woff') format('woff'),
         url('#{$path}#{map-get($face, file)}.woff2') format('woff2'),
         url('#{$path}#{map-get($face, file)}.ttf') format('truetype'),
         url('#{$path}#{map-get($face, file)}.svg##{map-get($face, family)}') format('svg');
    font-weight: map-get($face, weight);
    font-style: map-get($face, style);
  }
}

@include font-face($font-regular);
@include font-face($font-bold);
@include font-face($font-thin);
